const Select = ({ value, onSelect }) => (
  <div className="form-group">
    <select
      className="custom-select d-md-inline-block"
      value={value}
      onChange={onSelect}
    >
      <option value="USD">USD</option>
      <option value="GBP">GBP</option>
      <option value="EUR">EUR</option>
    </select>
  </div>
);

export default Select;
